.coder {   margin-top: 10px;   display: grid;   grid-template-columns: 1fr 1fr;   grid-gap: 10px; }   .coder .coder-item {     flex: 1;     line-height: 1.3em;     display: flex;     flex-direction: column;     background-color: var(--l-clr-base-light);     box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.2); }     .coder .coder-item span {       font-family: monospace; }     .coder .coder-item:before {       font-family: monospace;       font-size: 1rem;       font-weight: bold;       padding: 2px 15px; }     .coder .coder-item.js:before {       content: '<javascript/>';       background-color: var(--l-clr-base);       color: #777; }     .coder .coder-item.result .content {       padding: 15px; }     .coder .coder-item.result:before {       content: '<result/>';       background-color: var(--l-clr-base);       color: #777; }     .coder .coder-item pre code {       display: block;       flex: 1; }     .coder .coder-item .code-js {       content: '<code/>';       background-color: var(--l-clr-base-light);       padding: 15px;       font-family: monospace; }     .coder .coder-item .code-js,     .coder .coder-item .content {       max-height: 500px;       overflow: auto; } 