/* Lure.PopLog */ .l-pop-log-hook {   position: fixed;   right: 5px;   bottom: 5px;   z-index: 100;   width: 400px;   max-width: 20%;   min-width: 400px; }  .l-pop-log {   position: relative;   opacity: 0;   padding: 10px 20px;   margin-top: 5px;   background-color: #d00;   color: #fff;   transition: 500ms ease-in; }   .l-pop-log.notice {     background-color: #00598e; }   .l-pop-log.success {     background-color: var(--l-clr-success); }   .l-pop-log.warning {     background-color: var(--l-clr-warn); }   .l-pop-log.error {     background-color: var(--l-clr-error); }   .l-pop-log:hover .l-pop-log__close {     opacity: 0.7; }   .l-pop-log .l-pop-log__close {     height: 12px;     width: 12px;     position: absolute;     background: url(../../img/icon-x_white.svg) no-repeat center;     background-size: contain;     cursor: pointer;     opacity: 0;     top: 7px;     right: 7px; }     .l-pop-log .l-pop-log__close:hover {       opacity: 1; }   .l-pop-log .l-pop-log__life {     height: 5px;     width: 100%;     position: absolute;     left: 0;     bottom: 0;     transition: 4950ms linear;     background-color: rgba(0, 0, 0, 0.4); } 